.footer {
  position: fixed;
  bottom: 0;
  height: 65px;
  width: 100%;
  padding: 10px;
  background-color: #282828;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__left {
  flex: 0.3;
  display: flex;
  align-items: center;
  color: white;
  width: 300px;
}
.footer__albumLogo {
  height: 60px;
  width: 50px;
  margin-right: 20px;
  object-fit: contain;
}
.footer__center {
  flex: 0.4;
  padding: 0 100px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 300px;
}

.footer__right {
  flex: 0.3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.footer__right * .MuiSlider-root {
  color: green;
}
.footer__right * .MuiGrid-item {
  margin: auto;
}

.footer__green {
  color: #1ed15e;
}

.footer__songInfo > h4 {
  margin-bottom: 5px;
}

.footer__songInfo > p {
  font-size: 12px;
}

.footer__icon:hover,
.footer__green:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2);
}

@media screen and (max-width: 600px) {
  .footer {
    flex-direction: column-reverse;
    justify-content: center;
  }
  .footer__left {
    width: 100%;
    justify-content: center;
  }
  .footer__left img {
    display: none;
  }
  .footer__right {
    display: none;
  }
  .footer__center {
    width: 100%;
    height: 100%;
  }
}
